import React, { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '../../components/ui/card';
import { Button } from '../../components/ui/button';
import { Badge } from '../../components/ui/badge';
import { Input } from '../../components/ui/input';
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '../../components/ui/dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '../../components/ui/select';
import { Label } from '../../components/ui/label';
import { Textarea } from '../../components/ui/textarea';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { toast } from 'sonner';
import {
  Users,
  Plus,
  FileText,
  Calendar,
  AlertCircle,
  Search,
  Filter,
  UserPlus,
  Activity,
  Edit,
  Eye,
  Phone,
  Mail,
  MapPin,
  Clock,
  Loader2
} from 'lucide-react';
import {
  getAllStudyParticipants,
  getStudyParticipantStats,
  enrollParticipant,
  getAvailablePatients
} from '../../api/services/studyParticipant/studyParticipantService';
import { getAllClinicalStudies } from '../../api/services/clinicalStudy/clinicalStudyService';

const StudyParticipants = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [isEnrollDialogOpen, setIsEnrollDialogOpen] = useState(false);
  const [isAddPatientDialogOpen, setIsAddPatientDialogOpen] = useState(false);
  const [selectedStudy, setSelectedStudy] = useState('');
  const [selectedPatient, setSelectedPatient] = useState('');
  const [showFilters, setShowFilters] = useState(false);
  const [showSearch, setShowSearch] = useState(false);
  const [selectedParticipant, setSelectedParticipant] = useState(null);
  const queryClient = useQueryClient();

  // Fetch study participants
  const { data: participantsData, isLoading: participantsLoading, error: participantsError } = useQuery({
    queryKey: ['studyParticipants'],
    queryFn: getAllStudyParticipants,
    retry: 1,
  });

  // Fetch participant statistics
  const { data: statsData, isLoading: statsLoading } = useQuery({
    queryKey: ['studyParticipantStats'],
    queryFn: getStudyParticipantStats,
    retry: 1,
  });

  // Fetch available patients for enrollment
  const { data: availablePatientsData, isLoading: patientsLoading } = useQuery({
    queryKey: ['availablePatients'],
    queryFn: getAvailablePatients,
    retry: 1,
  });

  // Fetch clinical studies for enrollment
  const { data: studiesData, isLoading: studiesLoading } = useQuery({
    queryKey: ['clinicalStudies'],
    queryFn: getAllClinicalStudies,
    retry: 1,
  });

  const participants = participantsData?.data || [];
  const participantStats = statsData?.data || {
    total: 0,
    screening: 0,
    enrolled: 0,
    completed: 0,
    withdrawn: 0,
    screenFailed: 0
  };
  const availablePatients = availablePatientsData?.data || [];
  const studies = studiesData?.data || [];

  // Enrollment mutation
  const enrollMutation = useMutation({
    mutationFn: enrollParticipant,
    onSuccess: () => {
      toast.success('Patient enrolled successfully');
      queryClient.invalidateQueries({ queryKey: ['studyParticipants'] });
      queryClient.invalidateQueries({ queryKey: ['studyParticipantStats'] });
      setIsEnrollDialogOpen(false);
      setSelectedStudy('');
      setSelectedPatient('');
    },
    onError: (error) => {
      toast.error(error.response?.data?.message || 'Failed to enroll patient');
    },
  });

  const handleEnrollment = (data) => {
    enrollMutation.mutate({
      study_id: selectedStudy,
      patient_id: selectedPatient,
      ...data
    });
  };

  // Handler functions for button functionality
  const handleViewStudyParticipants = (study) => {
    toast.info(`Viewing participants for study: ${study.name}`);
    // TODO: Filter participants by study or navigate to study-specific view
  };

  const handleToggleSearch = () => {
    setShowSearch(!showSearch);
  };

  const handleToggleFilters = () => {
    setShowFilters(!showFilters);
  };

  const handleViewParticipant = (participant) => {
    setSelectedParticipant(participant);
    toast.info(`Viewing details for participant: ${participant.patient_name}`);
    // TODO: Open participant details modal or navigate to participant page
  };

  const handleEditParticipant = (participant) => {
    setSelectedParticipant(participant);
    toast.info(`Editing participant: ${participant.patient_name}`);
    // TODO: Open edit participant modal
  };

  const handleEnrollPatient = () => {
    if (!selectedStudy || !selectedPatient) {
      toast.error('Please select both study and patient');
      return;
    }

    toast.success('Patient enrolled successfully');
    setIsEnrollDialogOpen(false);
    setSelectedStudy('');
    setSelectedPatient('');
    // TODO: Implement actual enrollment logic
  };

  const handleAddNewPatient = () => {
    toast.success('New patient added successfully');
    setIsAddPatientDialogOpen(false);
    // TODO: Implement actual add patient logic
  };

  const getStatusBadge = (status) => {
    const statusColors = {
      'screening': 'bg-yellow-100 text-yellow-800',
      'enrolled': 'bg-green-100 text-green-800',
      'completed': 'bg-blue-100 text-blue-800',
      'withdrawn': 'bg-red-100 text-red-800',
      'screen_failed': 'bg-gray-100 text-gray-800'
    };
    return statusColors[status] || 'bg-gray-100 text-gray-800';
  };

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Study Participants</h1>
          <p className="text-gray-600 mt-1">Manage participant enrollment and study assignments</p>
        </div>
        
        <div className="flex space-x-2">
          <Dialog open={isEnrollDialogOpen} onOpenChange={setIsEnrollDialogOpen}>
            <DialogTrigger asChild>
              <Button variant="outline">
                <UserPlus className="h-4 w-4 mr-2" />
                Enroll from Patients
              </Button>
            </DialogTrigger>
            <DialogContent className="max-w-md">
              <DialogHeader>
                <DialogTitle>Enroll Patient in Study</DialogTitle>
              </DialogHeader>
              <div className="space-y-4">
                <div>
                  <Label htmlFor="patient">Select Patient</Label>
                  <Select value={selectedPatient} onValueChange={setSelectedPatient}>
                    <SelectTrigger>
                      <SelectValue placeholder="Choose patient" />
                    </SelectTrigger>
                    <SelectContent>
                      {availablePatients.map((patient) => (
                        <SelectItem key={patient.id} value={patient.id.toString()}>
                          {patient.name} - {patient.status}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Label htmlFor="study">Select Study</Label>
                  <Select value={selectedStudy} onValueChange={setSelectedStudy}>
                    <SelectTrigger>
                      <SelectValue placeholder="Choose study" />
                    </SelectTrigger>
                    <SelectContent>
                      {studies.map((study) => (
                        <SelectItem key={study.id} value={study.id.toString()}>
                          {study.name} ({study.code})
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Label htmlFor="notes">Enrollment Notes</Label>
                  <Textarea id="notes" placeholder="Enter any notes about enrollment" rows={3} />
                </div>
                <div className="flex justify-end space-x-2 pt-4">
                  <Button variant="outline" onClick={() => setIsEnrollDialogOpen(false)}>
                    Cancel
                  </Button>
                  <Button onClick={handleEnrollPatient}>
                    Enroll Patient
                  </Button>
                </div>
              </div>
            </DialogContent>
          </Dialog>

          <Dialog open={isAddPatientDialogOpen} onOpenChange={setIsAddPatientDialogOpen}>
            <DialogTrigger asChild>
              <Button>
                <Plus className="h-4 w-4 mr-2" />
                Add New Patient
              </Button>
            </DialogTrigger>
            <DialogContent className="max-w-md" aria-describedby="patient-form-description">
              <DialogHeader>
                <DialogTitle>Add New Patient</DialogTitle>
                <DialogDescription id="patient-form-description">
                  Fill out the form below to add a new patient to the study. All required fields must be completed.
                </DialogDescription>
              </DialogHeader>
              <div className="space-y-4">
                <div>
                  <Label htmlFor="patientName">Full Name</Label>
                  <Input id="patientName" placeholder="Enter patient name" />
                </div>
                <div>
                  <Label htmlFor="patientEmail">Email</Label>
                  <Input id="patientEmail" type="email" placeholder="patient@email.com" />
                </div>
                <div>
                  <Label htmlFor="patientPhone">Phone</Label>
                  <Input id="patientPhone" placeholder="+1-555-0123" />
                </div>
                <div>
                  <Label htmlFor="patientStudy">Assign to Study</Label>
                  <Select>
                    <SelectTrigger>
                      <SelectValue placeholder="Choose study (optional)" />
                    </SelectTrigger>
                    <SelectContent>
                      {studies.map((study) => (
                        <SelectItem key={study.id} value={study.id.toString()}>
                          {study.name}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <div className="flex justify-end space-x-2 pt-4">
                  <Button variant="outline" onClick={() => setIsAddPatientDialogOpen(false)}>
                    Cancel
                  </Button>
                  <Button onClick={handleAddNewPatient}>
                    Add Patient
                  </Button>
                </div>
              </div>
            </DialogContent>
          </Dialog>
        </div>
      </div>

      {/* Statistics Cards */}
      <div className="grid grid-cols-1 md:grid-cols-6 gap-4">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total</CardTitle>
            <Users className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{participantStats.total}</div>
          </CardContent>
        </Card>
        
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Screening</CardTitle>
            <Activity className="h-4 w-4 text-yellow-500" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{participantStats.screening}</div>
          </CardContent>
        </Card>
        
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Enrolled</CardTitle>
            <Users className="h-4 w-4 text-green-500" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{participantStats.enrolled}</div>
          </CardContent>
        </Card>
        
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Completed</CardTitle>
            <Activity className="h-4 w-4 text-blue-500" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{participantStats.completed}</div>
          </CardContent>
        </Card>
        
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Withdrawn</CardTitle>
            <Activity className="h-4 w-4 text-red-500" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{participantStats.withdrawn}</div>
          </CardContent>
        </Card>
        
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Screen Failed</CardTitle>
            <Activity className="h-4 w-4 text-gray-500" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{participantStats.screenFailed}</div>
          </CardContent>
        </Card>
      </div>

      {/* Study Overview */}
      <Card>
        <CardHeader>
          <CardTitle>Enrollment by Study</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {studies.map((study) => (
              <div key={study.id} className="flex items-center justify-between p-4 border border-gray-200 rounded-lg">
                <div className="flex-1">
                  <h3 className="font-medium text-gray-900">{study.name}</h3>
                  <p className="text-sm text-gray-500">Study Code: {study.code}</p>
                </div>
                
                <div className="flex items-center space-x-4">
                  <div className="text-center">
                    <div className="text-lg font-bold text-gray-900">
                      {study.participants}/{study.target}
                    </div>
                    <div className="text-xs text-gray-500">Participants</div>
                  </div>
                  
                  <div className="w-32">
                    <div className="flex justify-between text-xs text-gray-600 mb-1">
                      <span>Progress</span>
                      <span>{Math.round((study.participants / study.target) * 100)}%</span>
                    </div>
                    <div className="w-full bg-gray-200 rounded-full h-2">
                      <div 
                        className="bg-blue-600 h-2 rounded-full" 
                        style={{ width: `${(study.participants / study.target) * 100}%` }}
                      ></div>
                    </div>
                  </div>
                  
                  <Badge className={study.status === 'Recruiting' ? 'bg-blue-100 text-blue-800' : 'bg-green-100 text-green-800'}>
                    {study.status}
                  </Badge>
                  
                  <Button variant="outline" size="sm" onClick={() => handleViewStudyParticipants(study)}>
                    View Participants
                  </Button>
                </div>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Participants List */}
      <Card>
        <CardHeader>
          <div className="flex justify-between items-center">
            <CardTitle>All Participants</CardTitle>
            <div className="flex space-x-2">
              <Button variant="outline" size="sm" onClick={handleToggleSearch}>
                <Search className="h-4 w-4 mr-2" />
                Search
              </Button>
              <Button variant="outline" size="sm" onClick={handleToggleFilters}>
                <Filter className="h-4 w-4 mr-2" />
                Filter
              </Button>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          {participantsLoading ? (
            <div className="flex items-center justify-center py-12">
              <Loader2 className="h-8 w-8 animate-spin text-gray-400" />
              <span className="ml-2 text-gray-600">Loading participants...</span>
            </div>
          ) : participantsError ? (
            <div className="text-center py-12">
              <AlertCircle className="h-12 w-12 text-red-300 mx-auto mb-4" />
              <h3 className="text-lg font-semibold text-gray-900 mb-2">Error Loading Participants</h3>
              <p className="text-gray-600 mb-4">Failed to load study participants. Please try again.</p>
            </div>
          ) : participants.length === 0 ? (
            <div className="text-center py-12">
              <Users className="h-12 w-12 text-gray-300 mx-auto mb-4" />
              <h3 className="text-lg font-semibold text-gray-900 mb-2">No Participants Found</h3>
              <p className="text-gray-600 mb-4">No study participants have been enrolled yet.</p>
              <Button onClick={() => setIsEnrollDialogOpen(true)}>
                <UserPlus className="h-4 w-4 mr-2" />
                Enroll First Participant
              </Button>
            </div>
          ) : (
            <div className="space-y-4">
              {participants.map((participant) => (
              <div key={participant.id} className="border border-gray-200 rounded-lg p-6 hover:shadow-md transition-shadow">
                <div className="flex justify-between items-start mb-4">
                  <div>
                    <h3 className="text-lg font-semibold text-gray-900">{participant.patient_name}</h3>
                    <p className="text-sm text-gray-500">Participant ID: {participant.participant_id}</p>
                  </div>
                  <div className="flex items-center space-x-2">
                    <Badge className={getStatusBadge(participant.enrollment_status)}>
                      {participant.enrollment_status}
                    </Badge>
                    <Button variant="outline" size="sm" onClick={() => handleViewParticipant(participant)}>
                      <Eye className="h-4 w-4 mr-1" />
                      View
                    </Button>
                    <Button variant="outline" size="sm" onClick={() => handleEditParticipant(participant)}>
                      <Edit className="h-4 w-4 mr-1" />
                      Edit
                    </Button>
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-4">
                  <div className="flex items-start space-x-2">
                    <Mail className="h-4 w-4 text-gray-400 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Email</p>
                      <p className="text-sm text-gray-600">{participant.patient_email}</p>
                    </div>
                  </div>

                  <div className="flex items-start space-x-2">
                    <Phone className="h-4 w-4 text-gray-400 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Phone</p>
                      <p className="text-sm text-gray-600">{participant.patient_phone}</p>
                    </div>
                  </div>

                  <div className="flex items-start space-x-2">
                    <Calendar className="h-4 w-4 text-gray-400 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Enrolled</p>
                      <p className="text-sm text-gray-600">{participant.enrollment_date}</p>
                    </div>
                  </div>

                  <div className="flex items-start space-x-2">
                    <MapPin className="h-4 w-4 text-gray-400 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Site</p>
                      <p className="text-sm text-gray-600">{participant.site}</p>
                    </div>
                  </div>
                </div>

                <div className="mb-4">
                  <p className="text-sm font-medium mb-1">Study Information:</p>
                  <div className="flex items-center space-x-4">
                    <Badge variant="outline">{participant.study_code}</Badge>
                    <span className="text-sm text-gray-600">{participant.study}</span>
                    <span className="text-sm text-gray-500">PI: {participant.pi}</span>
                  </div>
                </div>

                {participant.notes && (
                  <div className="pt-4 border-t border-gray-100">
                    <p className="text-sm font-medium mb-1">Notes:</p>
                    <p className="text-sm text-gray-600">{participant.notes}</p>
                  </div>
                )}
              </div>
            ))}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Quick Actions */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <Card className="cursor-pointer hover:shadow-md transition-shadow">
          <CardContent className="p-6 text-center">
            <UserPlus className="h-8 w-8 text-blue-600 mx-auto mb-3" />
            <h3 className="font-medium text-gray-900 mb-2">Enroll from Patients</h3>
            <p className="text-sm text-gray-600">
              Select qualified patients from your database and enroll them in studies
            </p>
          </CardContent>
        </Card>
        
        <Card className="cursor-pointer hover:shadow-md transition-shadow">
          <CardContent className="p-6 text-center">
            <Plus className="h-8 w-8 text-green-600 mx-auto mb-3" />
            <h3 className="font-medium text-gray-900 mb-2">Add New Participant</h3>
            <p className="text-sm text-gray-600">
              Manually add a new participant and assign them to a study
            </p>
          </CardContent>
        </Card>
        
        <Card className="cursor-pointer hover:shadow-md transition-shadow">
          <CardContent className="p-6 text-center">
            <FileText className="h-8 w-8 text-purple-600 mx-auto mb-3" />
            <h3 className="font-medium text-gray-900 mb-2">Import Participants</h3>
            <p className="text-sm text-gray-600">
              Bulk import participants from CSV or Excel files
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Development Notice */}
      <Card className="border-yellow-200 bg-yellow-50">
        <CardContent className="flex items-center space-x-3 pt-6">
          <AlertCircle className="h-5 w-5 text-yellow-600" />
          <div>
            <h3 className="font-medium text-yellow-800">Study Participants System</h3>
            <p className="text-sm text-yellow-700">
              This is a preview of the Study Participants management system. Full functionality including 
              participant enrollment, study assignment, status tracking, and integration with the patient 
              database will be implemented in the next phase.
            </p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default StudyParticipants;
